body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
body {
  margin: 0;
  padding: 0;
  font-family: 'Work Sans',sans-serif;
  background:#f4f4ef;
  }
  
  
  .center {
  text-align: center;
  }
  a {
  color:#636363;
  text-decoration: none;
  transition:all 0.5s ease-in;
  }
  
  
  a:hover {
  text-decoration: underline;
  }
  p.post_message {
    margin: 3px 0;
}
  
  /* Navigation */
  
  
  .navbar {
  background:#fff;
  text-align:center;
  padding:0.5rem 0;
  width:100%;
  display:flex;
  justify-content: center;
  align-items: center;
  box-shadow:0 5px 15px 0 rgba(46, 61, 73, 0.12);
  }
  
  
  .navbar h2 {
  margin:0;
  font-weight:200;
  font-size:30px;
  letter-spacing: 4px;
  font-family:'Work Sans',sans-serif;
  }
  
  .post-container + div {
    float: right;
    width: 50%;
}
  /* Post Styles */
  .post-container {
    background: #fff;
    padding: 50px;
    float: left;
    width: 29%;
    margin: 40px;
    box-shadow: 0 5px 15px 0 rgba(46, 61, 73, 0.12);
    font-family: 'Work Sans',sans-serif;
  }
  
  
  .post_heading {
    font-weight: 400;
    font-size: 12px;
    color: #636363;
    margin: 17px 17px 5px;
    text-align: right;
  }
  
  
  .form {
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  }
  
  
  .form input {
  height:44px;
  padding-left:15px;
  padding-right:15px;
  border: 1px solid #dbe2e8;
  font-size:14px;
  border-radius:2px;
  color:#636363;
  box-shadow: 0 2px 2px 0 rgba(46, 60, 73, 0.05);
  outline:none;
  width:80%;
  }
  
  
  .form textarea {
  width:80%;
  max-width:80%;
  padding-left:15px;
  padding-right:15px;
  padding-top:15px;
  border: 1px solid #dbe2e8;
  font-size:14px;
  border-radius:2px;
  color:#636363;
  font-family: 'Work Sans,',sans-serif;
  box-shadow: 0 2px 2px 0 rgba(46, 60, 73, 0.05);
  outline:none;
  }
  
  
  /* All Posts */
  
  
  .all_post_heading {
  text-align:center;
  font-weight:400;
  font-size:40px;
  color:#636363;
  word-wrap: break-word;
  }
  
  
  .post {
    background: #fff;
    width: 84%;
    padding: 4px 7px;
    float: right;
    margin: 9px;
    box-shadow: 10px 10px 12px 0 rgba(46, 60, 73, 0.05);
  }
  
  
  .form button {
  background:#02b3e4;
  padding:15px 40px;
  text-align: center;
  color:#fff;
  font-family:'Work Sans',sans-serif;
  border-radius:4px;
  border:none;
  font-size:20px;
  }
  
  
  .post_title {
    margin: 0px;
  font-weight:400;
  font-size:15px;
  color:#636363;
 
  }
  
  
  
  
  .control-buttons {
  display:flex;
  }
  
  
  .delete {
  background: #ff7777;
  
  border: none;
  font-size: 15px;
  padding: 5px 10px;
  color: #fff;
  float: right;
  }
  
  
  .edit {
    background: #02b3e4;
    border: none;
    font-size: 15px;
    padding: 5px 10px;
    color: #fff;
    float: right;
  }